import React, { useState, useEffect } from "react";
import {
  CCard,
  CCardBody,
  CCardHeader,
  CCol,
  CRow,
  CForm,
  CButton,
  CFormLabel,
  CFormInput,
  CFormFeedback,
  CButtonGroup,
} from "@coreui/react";

import { Authentification } from "../../components/index";

import { useForm } from "react-hook-form";
import { yupResolver } from "@hookform/resolvers/yup";
import * as yup from "yup";

import { useHistory, useParams } from "react-router-dom";

import Message from "../../components/Message";

import { getOptions, addOption } from "../../provider/optionFetch";

const schemaUser = yup.object().shape({
  name: yup.string().required("name is required"),
  price: yup.number().required("price is required"),
});

const AjoutOption = () => {
  /** State */
  const [message, setMessage] = useState(null);
  const [success, setSuccess] = useState(false);
  const [error, setError] = useState(false);
  const [option, setOption] = useState(false);

  /** Hook Route */
  let history = useHistory();
  let { id: idOption } = useParams();
  const paramQuery = window.location.hash;
  const paramsJS = new URLSearchParams(
    paramQuery.substring(paramQuery.lastIndexOf("?"))
  );
  const optionList = paramsJS.has("optionList") ? paramsJS.get("optionList") : [];

  /** Form Hook */
  const {
    register,
    handleSubmit,
    formState: { errors, isSubmitting, isValid },
    // resetField,
    setValue,
  } = useForm({
    resolver: yupResolver(schemaUser),
    mode: "onTouched",
  });

  /** Run once, when the component mounts */
  useEffect(() => {
    console.log("Run once, when the component mounts");

    if (idOption) {
      async function fetch() {
        const resp = await getOptions(idOption);

        if (resp?.status) {
          setValue("name", resp?.data.name);
          setValue("price", resp?.data.price);
        } else {
          setMessage(resp?.error);
        }
      }

      fetch();
    }
  }, [idOption, setValue]);

  const onSubmit = async (data) => {
    let resp = null;
    // optionList Default
    const body = {...data, optionList: "615f3771a12ecf47502c851b"}

    if (isValid) {
      /** reste */
      setSuccess(false);
      setError(false);
      resp = await addOption(body, idOption);
    }

    if (resp?.status) {
      /** Reset */
      // resetField("name");
      // resetField("price");

      setOption(resp?.data?._id)

      setSuccess(true);
    } else {
      setError(true);
      setMessage(resp?.error);
    }
  };

  const back = () => {
    const params = option ? [option] : []
    if (idOption) {
      history.push(`/option-list/${optionList}`);
    } else {
      history.push(`/ajout-optionList?options=${params}`);
    }
    
  };

  return (
    <CRow>
      <Authentification />
      {error && <Message> {message}</Message>}
      {success && <Message> {`Success Ajout ou Edite`}</Message>}
      <CCol xs={12}>
        <CCard className="mb-4">
          <CCardHeader>
            <CRow>
              <CCol sm={5}>
                <strong>Ajout Option</strong>
              </CCol>
              <CCol sm={7}>
                <CButtonGroup className="float-end me-3">
                  <CButton
                    color="outline-secondary"
                    className="mx-0"
                    onClick={() => back()}
                  >
                    Retour
                  </CButton>
                </CButtonGroup>
              </CCol>
            </CRow>
          </CCardHeader>
          <CCardBody>
            <CForm
              className={`row g-3 needs-validation ${
                isValid && "was-validated"
              }`}
              onSubmit={handleSubmit(onSubmit)}
            >
              <CCol md={6}>
                <CFormLabel htmlFor="name">Name</CFormLabel>
                <CFormInput
                  type="text"
                  id="name"
                  required
                  {...register("name")}
                />
                <CFormFeedback invalid>{errors.name?.message}</CFormFeedback>
              </CCol>
              <CCol md={6}>
                <CFormLabel htmlFor="price">Price</CFormLabel>
                <CFormInput
                  type="number"
                  step={"any"}
                  id="price"
                  required
                  {...register("price")}
                />
                <CFormFeedback invalid>{errors.price?.message}</CFormFeedback>
              </CCol>
              <CCol xs={12}>
                <CButton
                  color="primary"
                  type="submit"
                  className={isSubmitting ? "disabled" : " "}
                >
                  Submit
                </CButton>
              </CCol>
            </CForm>
          </CCardBody>
        </CCard>
      </CCol>
    </CRow>
  );
};
export default AjoutOption;
