import axios from 'axios'
import { variable } from '../configs/variable'

export const getOptions = async (id = null) => {
    try {

        const config = {
            headers: {
                'Content-Type': 'application/json'
            }
        }

        const endpoint = variable.env === 'local' ? variable.endpointLocal : variable.endpointProd

        const url  = id ? `${endpoint}/api/options/${id}` : `${endpoint}/api/options`

        const { data } = await axios.get(`${url}`, config)

        return {status: true, data}
    } catch (error) {
        const message = error.response && error.response.data.message
                ? error.response.data.message
                : error.message
        
        return {status: false, error: message}
    }
}

export const deleteOption = async (id) => {
    try {

        const config = {
            headers: {
                'Content-Type': 'application/json'
            }
        }
        
        const endpoint = variable.env === 'local' ? variable.endpointLocal : variable.endpointProd

        const { data } = await axios.delete(`${endpoint}/api/options/${id}`, config)

        return {status: true, data}
    } catch (error) {
        const message = error.response && error.response.data.message
                ? error.response.data.message
                : error.message
        
        return {status: false, error: message}
    }
}

export const addOption = async (option, id = null) => {
    try {

        const config = {
            headers: {
                'Content-Type': 'application/json'
            }
        }

        const endpoint = variable.env === 'local' ? variable.endpointLocal : variable.endpointProd

        let reponse = null

        if (id) {
            reponse =  await axios.put(`${endpoint}/api/options/${id}`, option, config)
        } else {
            reponse =  await axios.post(`${endpoint}/api/options`, option, config)
        }

        return {status: true, data: reponse?.data}
    } catch (error) {
        const message = error.response && error.response.data.message
                ? error.response.data.message
                : error.message
        
        return {status: false, error: message}
    }
}